import { mapState } from 'vuex';
import { updateProfileService } from './service';
export default {
  name: "Settings",
  middleware: ["authenticated"],
  computed: {
    ...mapState(['user']),
  },
  data() {
    return {
      form: { }
    }
  },
  mounted() {
    this.form = {
      ...this.user
    };
  },
  methods: {
    logout() {
      this.$store.commit('clearUser');
      this.$router.push('/');
    },
    async updateUser() {
      const { data } = await updateProfileService(this.form);
      this.$store.commit("setUser", data.user);
    }
  }
};
